.about-us-page {
    padding: 20px;
  }
  
  .thumbnails {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .thumbnail {
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%; /* Make the thumbnail circular */
  }
  
  .thumbnail-name {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
    padding: 5px;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    font-size: 14px;
    text-align: center;
    width: 100%;
    box-sizing: border-box;
  }
  
  .thumbnail-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%; /* Make the image within the thumbnail circular */
    transition: opacity 0.3s ease-in-out;
  }
  
  .thumbnail-image-hover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    border-radius: 50%; /* Make the hover image circular */
    transition: opacity 0.3s ease-in-out;
  }
  
  .thumbnail:hover .thumbnail-image {
    opacity: 0;
  }
  
  .thumbnail:hover .thumbnail-image-hover {
    opacity: 1;
  }